import {_decorator, Component, game, Mesh, MeshRenderer, Node, profiler, Quat, Size, Vec4} from 'cc';

const {ccclass, property} = _decorator;

class Game_Board {
  grid_size: Size;
  renderable: MeshRenderer;

  public constructor(grid_size: Size, renderable: MeshRenderer) {
    this.grid_size = grid_size;
    this.renderable = renderable;
  }
  public render() {
    console.log(this.renderable.material);
    this.renderable.material.setProperty(
        'tilingOffset', new Vec4(this.grid_size.x, this.grid_size.y, 0, 0));
  }
}

@ccclass('Main')
export class Main extends Component {
  @property(MeshRenderer) board_renderable;
  start() {
    let game_board = new Game_Board(new Size(8, 8), this.board_renderable);

    game_board.render();
  }
}
